import back from '@/components/back'

export default {
    components:{
        back
    },
    data(){
        return{
            tab: ['不吸烟', '已戒烟', '吸烟'],
            activeTab: 0,
            dayNum: 1,
            age: 1,
            patient: this.$store.state.patient
        }
    },
    created(){
        const info = this.$store.state.Health
        if(info){
            this.activeTab = info.SmokingHabit.SmokingHabit - 1
            if(this.activeTab == 2){
                this.dayNum = info.SmokingHabit.SmokingNum
                this.age = info.SmokingHabit.SmokingAge
            }
        }
    },
    methods:{
        operate(i,t){
            if(t == 'del'){
                this[i] -= 1
            }
            else{
                this[i] += 1
            }
            if(this[i] < 0){
                this[i] = 0
            }
        },
        send(){
            const SmokingNum = this.activeTab == 2 ? this.dayNum : 0
            const SmokingAge = this.activeTab == 2 ? this.age : 0
            this.$post('PlatFormAPI/PatientAccount/SaveSmokingHabit',{
                PatientId: this.patient.PatientId,
                SmokingHabit: this.activeTab + 1,
                SmokingNum,
                SmokingAge,
            }).then(o => {
            	this.$store.dispatch('pushIsFresh',true)
                this.$toast('保存成功')
                this.$router.back()
            })
        }
    }
}



